const getDefaultResolution = (streams) => Object.keys(streams).find(key => streams[key] == streams["default"] && key !== "default");
const classFromStatus = (status) => ["success", "failure"].includes(status) ? `item-to-add--${status}` : "";
const allowTabInTextArea = function (e) {
    // https://stackoverflow.com/questions/6637341/use-tab-to-indent-in-textarea
    const src = e.currentTarget;
    if (e.key == 'Tab') {
        e.preventDefault();
        var start = src.selectionStart;
        var end = src.selectionEnd;
        // set textarea value to: text before caret + tab + text after caret
        src.value = src.value.substring(0, start) +
            "\t" + src.value.substring(end);
        // put caret at right position again
        src.selectionStart =
            src.selectionEnd = start + 1;
    }
};
